<template>
  <div>
    <img style="width:1174px;margin-bottom:15px" src="./report3pic.png" />
    <div id="div1" class="eDiv"></div>
  </div>
</template>
<script>
import currentLocate from "@/components/common/tools/currentLocate";
export default {
  data() {
    return {};
  },
  mounted: function() {
    var option1 = {
      title: {
        text: "",
        subtext: ""
      },
      tooltip: {
        trigger: "axis"
      },
      legend: {
        data: [""]
      },
      toolbox: {
        show: true,
        feature: {
          mark: { show: true },
          dataView: { show: true, readOnly: false },
          magicType: { show: true, type: ["line", "bar"] },
          restore: { show: true },
          saveAsImage: { show: true }
        }
      },
      calculable: true,
      xAxis: [
        {
          type: "category",
          data: [
            "",
            "",
            "",
            "",
            "",
            "",
            "",
            "",
            "",
            "",
            "",
            "",
            "",
            "",
            "",
            "",
            "",
            "",
            "",
            "",
            ""
          ]
        }
      ],
      yAxis: [
        {
          type: "value"
        }
      ],
      series: [
        {
          name: "",
          type: "bar",
          data: [
            11,
            8,
            0,
            2,
            0,
            2,
            0,
            6,
            0,
            3,
            0,
            0,
            32,
            0,
            1,
            46,
            17,
            616,
            138,
            475,
            1097
          ],
          markLine: {
            data: [{ type: "average", name: "" }]
          }
        }
      ]
    };
    var myChart1 = this.echarts.init(document.getElementById("div1"));
    myChart1.setOption(option1);
  }
};
</script>


